@import '../../sass/Variables.scss';
.my-descryption {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	width: 80%;
	max-width: 45rem;
	margin: 3rem 2rem 4rem 2rem;
	font-size: 1.8rem;
	text-indent: 1.5em;

	&__edit-btn {
		width: 50%;
		margin: 2rem 2rem 0.5rem 2rem;
		padding: 0.6rem 1.8rem;
		font-size: 1.6rem;
		border-radius: 8px;
		transition: background-color 0.3s;
	}
	&__edit-btn:hover {
		cursor: pointer;
	}
}
.my-descryption::first-letter {
	font-size: 3rem;
}
.my-descryption::after {
	content: ' ';
	display: block;
	position: absolute;
	right: 0%;
	bottom: 0%;
	width: 30%;
	height: 30%;
	margin: -15px -15px;
	border-bottom: 1px solid $primary-color;
	border-right: 1px solid $primary-color;
	border-bottom-right-radius: 8px;
	box-shadow: 3px 3px 4px -2px rgba(0, 0, 0, 0.3);
}
.my-descryption::before {
	content: ' ';
	display: block;
	position: absolute;
	left: 0%;
	top: 0%;
	width: 30%;
	height: 30%;
	margin: -15px -15px;
	border-top: 1px solid $primary-color;
	border-left: 1px solid $primary-color;
	border-top-left-radius: 8px;
	box-shadow: -3px -3px 4px -2px rgba(0, 0, 0, 0.3);
}

#light .my-descryption {
	color: $black;
	&__edit-btn {
		color: $white;
		background-color: $third-color;
		border: 1px solid $primary-color;
	}
	&__edit-btn:hover {
		background-color: $primary-color;
		border: 1px solid $third-color;
	}
}
#dark .my-descryption {
	color: $white;
	&__edit-btn {
		color: $white;
		background-color: $primary-color;
		border: 1px solid $white;
	}
	&__edit-btn:hover {
		background-color: $third-color;
		border: 1px solid $primary-color;
	}
}

@media (min-width: 1600px) {
	.my-descryption {
		justify-self: center;
		align-self: center;

		grid-column: 3/4;
		grid-row: 2/3;
		// margin: 7rem 2rem 4rem 2rem;
	}
}
